.hero-img{
    width: 100%;
    background-color: rgba(87, 129, 131,0.5);
    height: 100vh;
    position: center;
}
.hero-img::before{
   content: "";
   background: url("https://media.istockphoto.com/id/1346698461/photo/agile-software-development-with-developer-using-kanban-board-framework-methodology-on.jpg?s=1024x1024&w=is&k=20&c=LJek1nChXY5kJ4OIg-e7WeQl4BExvZfvhMkuOFkt7V4=");
   background-size: cover;
   background-position: center top;
   height: 100%;
   width: 100%;
   position: absolute;
   top: 0;
   left: 0;
   z-index: -1;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hero-img h1{
   font-size: 2.4rem;
}
.hero-img p{
    font-size: 1.4rem;
    text-align: center;
}
@media screen and (max-width: 640px){
    .hero-img h1{
        font-size: 2rem;
     }
   
}