label{
    color: #fff;
    margin-bottom: 0.5rem;
}
form{
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}
form input, form textarea{
    margin-bottom: 1rem;
    padding: 10px 20px;
    font-size: 1.2rem;
    background-color: rgba(66, 103, 105, 0.8);
    border-color: rgb(66, 103, 105);
    color: #f4f4f4;
    border: none;
}