.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}
.home-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(87, 129, 131,0.9);
    opacity: 0.8;
}
.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.hero .content h1{
    font-size: 4rem;
    padding: 5px 0 20px;
}
.hero .content p{
    font-size: 20px;
    font-weight: 200;
    text-transform: uppercase;
}
.content .btn{
    margin: 10px 5px;
}
@media screen and (max-width: 640px) {
    .hero .content h1{
        font-size: 3rem;
       
    }
    .hero .content p{
        font-size: 20px;
    }
}