.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(87, 129, 131,0.8);
    transition: 0.5s;
}

.manu{
    display: flex;
}
.manu li{
    padding: 0 20px;
    list-style: none;
}
.manu li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px){
    .manu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(87, 129, 131,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.4s;
    }
    .manu.active{
        left: 0;
    }
    .manu li{
        padding: 1rem 0;
    }
    .manu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: initial;
    }
}
